Office.onReady(() => {
  // Office.js is ready
});

// Debug function to log all table data
async function logAllTableData(context) {
  const tables = context.document.body.tables;
  tables.load("items");
  await context.sync();
  for (let t = 0; t < tables.items.length; t++) {
    const table = tables.items[t];
    const rows = table.rows;
    rows.load("items");
    await context.sync();
    for (let r = 0; r < rows.items.length; r++) {
      const row = rows.items[r];
      row.cells.load("items");
      await context.sync();
      let rowData = [];
      for (let c = 0; c < row.cells.items.length; c++) {
        const cell = row.cells.items[c];
        cell.body.load("text");
      }
      await context.sync();
      for (let c = 0; c < row.cells.items.length; c++) {
        const cell = row.cells.items[c];
        rowData.push(cell.body.text);
      }
      console.log(`Table ${t}, Row ${r}:`, rowData);
    }
  }
}

async function action(event) {
  try {
    await Word.run(async (context) => {
      // Log all table data for debugging
      await logAllTableData(context);
      const tables = context.document.body.tables;
      tables.load("items");
      await context.sync();

      for (const table of tables.items) {
        const rows = table.rows;
        rows.load("items");
        await context.sync();

        // Load all cells in all rows to read text later
        for (const row of rows.items) {
          row.cells.load("items");
        }
        await context.sync();

        // --------------------------
        // Subtotal / Total logic (works for both left and right sections)
        // --------------------------
        // Load all cell texts for all rows
        for (const row of rows.items) {
          for (const cell of row.cells.items) {
            cell.body.load("text");
          }
        }
        await context.sync();

        for (let i = 0; i < rows.items.length; i++) {
          const row = rows.items[i];
          for (let c = 0; c < row.cells.items.length; c++) {
            const cell = row.cells.items[c].body;
            const label = cell.text.trim().toUpperCase();
            if (label === "SUBTOTAL" || label === "TOTAL") {
              // Find the $ cell in the same row (left or right of label)
              let dollarCol = -1;
              let valueCol = -1;
              // Search right
              if (c + 1 < row.cells.items.length && row.cells.items[c + 1].body.text.trim() === "$" && c + 2 < row.cells.items.length) {
                dollarCol = c + 1;
                valueCol = c + 2;
              }
              // Search left
              else if (c - 1 >= 0 && row.cells.items[c - 1].body.text.trim() === "$" && c < row.cells.items.length) {
                dollarCol = c - 1;
                valueCol = c;
              }
              // If not found, skip
              if (dollarCol === -1 || valueCol < 0 || valueCol >= row.cells.items.length) continue;
              const valueCell = row.cells.items[valueCol].body;

              // Sum all values above in the same column (valueCol), where the $ cell is present in the same column in previous rows
              let sum = 0;
              let foundAny = false;
              for (let j = i - 1; j >= 0; j--) {
                const prevRow = rows.items[j];
                if (prevRow.cells.items.length <= valueCol || prevRow.cells.items.length <= dollarCol) break;
                const prevDollarCell = prevRow.cells.items[dollarCol].body;
                const prevValueCell = prevRow.cells.items[valueCol].body;
                // Stop if we hit another subtotal/total in the same label column
                if (prevRow.cells.items.length > c) {
                  const prevLabelCell = prevRow.cells.items[c].body;
                  const prevLabel = prevLabelCell.text.trim().toUpperCase();
                  if (prevLabel === "SUBTOTAL" || prevLabel === "TOTAL") break;
                }
                if (prevDollarCell.text.trim() === "$") {
                  const num = parseFloat(prevValueCell.text.trim().replace(/,/g, ""));
                  if (!isNaN(num)) {
                    sum += num;
                    foundAny = true;
                  }
                }
              }
              // Only update if the value cell is a placeholder (like x,xxx.xx)
              const placeholderPattern = /^[xX\s,.$0]+$/;
              if (foundAny && placeholderPattern.test(valueCell.text.trim())) {
                const formattedSum = sum.toLocaleString("en-US", {
                  style: "currency",
                  currency: "USD",
                });
                valueCell.clear();
                valueCell.insertText(formattedSum, Word.InsertLocation.start);
                await context.sync();
              }
            }
          }
        }

        // --------------------------
        // Income Source Table Logic (4 Columns) - Updated search
        // --------------------------

        // First, load all needed cells for all rows (for searching headers)
        for (const row of rows.items) {
          for (let c = 0; c < row.cells.items.length - 1; c++) {
            row.cells.items[c].body.load("text");
            row.cells.items[c + 1].body.load("text");
          }
        }
        await context.sync();

        let incomeSourceRowIndex = -1;

        // Search for "Income Source" and "Amount Received/Month" in adjacent cells in same row
        for (let i = 0; i < rows.items.length; i++) {
          const row = rows.items[i];
          for (let c = 0; c < row.cells.items.length - 1; c++) {
            const cellText = row.cells.items[c].body.text.trim().toUpperCase();
            const nextCellText = row.cells.items[c + 1].body.text.trim().toUpperCase();

            if (
              cellText === "INCOME SOURCE" &&
              nextCellText === "AMOUNT RECEIVED/MONTH"
            ) {
              incomeSourceRowIndex = i;
              break;
            }
          }
          if (incomeSourceRowIndex !== -1) break;
        }

        if (incomeSourceRowIndex !== -1) {
          let sum = 0;

          // Assume last two rows are Total and Annual
          const dataRows = rows.items.slice(incomeSourceRowIndex + 1, -2);

          // Load the $ and value cells in data rows for calculation
          for (const row of dataRows) {
            if (row.cells.items.length >= 4) {
              row.cells.items[2].body.load("text"); // $ symbol
              row.cells.items[3].body.load("text"); // value
            }
          }
          await context.sync();

          for (const row of dataRows) {
            if (row.cells.items.length < 4) continue;

            const dollar = row.cells.items[2].body.text.trim();
            const value = row.cells.items[3].body.text.trim();

            if (dollar === "$") {
              const num = parseFloat(value.replace(/,/g, ""));
              if (!isNaN(num)) sum += num;
            }
          }

          // Update total and annual rows
          const totalRow = rows.items[rows.items.length - 2];
          const annualRow = rows.items[rows.items.length - 1];

          if (
            totalRow.cells.items.length >= 4 &&
            annualRow.cells.items.length >= 4
          ) {
            const totalCell = totalRow.cells.items[3].body;
            const annualCell = annualRow.cells.items[3].body;

            totalCell.clear();
            totalCell.insertText(
              sum.toLocaleString("en-US", {
                style: "currency",
                currency: "USD",
              }),
              Word.InsertLocation.start
            );

            annualCell.clear();
            annualCell.insertText(
              (sum * 12).toLocaleString("en-US", {
                style: "currency",
                currency: "USD",
              }),
              Word.InsertLocation.start
            );

            await context.sync();
          }
        }

        // Block-wise section/subtotal logic for two-sections-per-row tables
        // --------------------------
        // Load all cell texts for all rows
        for (const row of rows.items) {
          for (const cell of row.cells.items) {
            cell.body.load("text");
          }
        }
        await context.sync();

        // Track running sums and section starts for left and right
        let leftSum = 0, rightSum = 0;
        let leftStart = 0, rightStart = 0;
        for (let i = 0; i < rows.items.length; i++) {
          const row = rows.items[i];
          const cells = row.cells.items.map(cell => cell.body.text.trim());
          // Section title row (3 or 5 cells, with non-empty label in left or right)
          if ((cells.length === 3 || cells.length === 5) && (cells[0] && cells[0] !== "$" && cells[0].toUpperCase() === cells[0])) {
            leftSum = 0;
            leftStart = i + 1;
          }
          if ((cells.length === 3 || cells.length === 5) && (cells[cells.length - 1] && cells[cells.length - 1] !== "$" && cells[cells.length - 1].toUpperCase() === cells[cells.length - 1])) {
            rightSum = 0;
            rightStart = i + 1;
          }
          // Data row (7 cells, with $ in cell 1 and/or 5)
          if (cells.length === 7) {
            // Left block
            if (cells[1] === "$" && cells[2] && !isNaN(parseFloat(cells[2].replace(/,/g, "")))) {
              leftSum += parseFloat(cells[2].replace(/,/g, ""));
            }
            // Right block
            if (cells[5] === "$" && cells[6] && !isNaN(parseFloat(cells[6].replace(/,/g, "")))) {
              rightSum += parseFloat(cells[6].replace(/,/g, ""));
            }
          }
          // Subtotal/total row (left)
          if (cells.length >= 3 && (cells[0].toUpperCase() === "SUBTOTAL" || cells[0].toUpperCase() === "TOTAL") && cells[1] === "$") {
            const valueCell = row.cells.items[2].body;
            const placeholderPattern = /^[xX\s,.$0]+$/;
            if (leftSum !== 0 && placeholderPattern.test(cells[2])) {
              const formattedSum = leftSum.toLocaleString("en-US", { style: "currency", currency: "USD" });
              valueCell.clear();
              valueCell.insertText(formattedSum, Word.InsertLocation.start);
              await context.sync();
            }
            leftSum = 0;
            leftStart = i + 1;
          }
          // Subtotal/total row (right)
          if (cells.length >= 7 && (cells[4].toUpperCase() === "SUBTOTAL" || cells[4].toUpperCase() === "TOTAL") && cells[5] === "$") {
            const valueCell = row.cells.items[6].body;
            const placeholderPattern = /^[xX\s,.$0]+$/;
            if (rightSum !== 0 && placeholderPattern.test(cells[6])) {
              const formattedSum = rightSum.toLocaleString("en-US", { style: "currency", currency: "USD" });
              valueCell.clear();
              valueCell.insertText(formattedSum, Word.InsertLocation.start);
              await context.sync();
            }
            rightSum = 0;
            rightStart = i + 1;
          }
          // Subtotal/total row (right, 5 cells)
          if (cells.length === 5 && (cells[2].toUpperCase() === "SUBTOTAL" || cells[2].toUpperCase() === "TOTAL") && cells[3] === "$") {
            const valueCell = row.cells.items[4].body;
            const placeholderPattern = /^[xX\s,.$0]+$/;
            if (rightSum !== 0 && placeholderPattern.test(cells[4])) {
              const formattedSum = rightSum.toLocaleString("en-US", { style: "currency", currency: "USD" });
              valueCell.clear();
              valueCell.insertText(formattedSum, Word.InsertLocation.start);
              await context.sync();
            }
            rightSum = 0;
            rightStart = i + 1;
          }
        }

        // Asset Table Calculation Logic
        // --------------------------
        let assetTotal = 0;
        let processedRows = 0;
        let allValues = []; // Array to store all processed values
        console.log("Starting asset table calculation...");
        
        // First, let's log the total number of rows we're processing
        console.log(`Total number of rows to process: ${rows.items.length}`);
        
        for (let i = 0; i < rows.items.length; i++) {
          const row = rows.items[i];
          const cells = row.cells.items.map(cell => cell.body.text.trim());
          
          // Log every row's details for debugging
          console.log(`\nRow ${i + 1}:`);
          console.log(`Number of cells: ${cells.length}`);
          console.log(`Cell contents:`, cells);
          
          // Check for rows with 4, 5, or 6 cells
          if (cells.length === 4 || cells.length === 5 || cells.length === 6) {
            let processed = false;
            let value = 0;
            
            // For rows with 4 cells, check if last two cells are $ and number
            if (cells.length === 4 && cells[2] === "$") {
              value = parseFloat(cells[3]) || 0;
              if (!isNaN(value)) {
                assetTotal += value;
                allValues.push({ row: i + 1, value: value });
                processedRows++;
                processed = true;
                console.log(`✓ Row ${i + 1}: Processed value ${value} from cells:`, cells);
              }
            }
            // For rows with 5 cells, check if last two cells are $ and number
            else if (cells.length === 5 && cells[3] === "$") {
              value = parseFloat(cells[4]) || 0;
              if (!isNaN(value)) {
                assetTotal += value;
                allValues.push({ row: i + 1, value: value });
                processedRows++;
                processed = true;
                console.log(`✓ Row ${i + 1}: Processed value ${value} from cells:`, cells);
              }
            }
            // For rows with 6 cells, check if last two cells are $ and number
            else if (cells.length === 6 && cells[4] === "$") {
              value = parseFloat(cells[5]) || 0;
              if (!isNaN(value)) {
                assetTotal += value;
                allValues.push({ row: i + 1, value: value });
                processedRows++;
                processed = true;
                console.log(`✓ Row ${i + 1}: Processed value ${value} from cells:`, cells);
              }
            }
            
            // Log rows that match the length but weren't processed
            if (!processed) {
              console.log(`✗ Row ${i + 1}: Not processed. Reasons:`);
              if (cells.length === 4) {
                console.log(`  - Cell[2] is "${cells[2]}" (expected "$")`);
                console.log(`  - Cell[3] is "${cells[3]}" (should be a number)`);
              } else if (cells.length === 5) {
                console.log(`  - Cell[3] is "${cells[3]}" (expected "$")`);
                console.log(`  - Cell[4] is "${cells[4]}" (should be a number)`);
              } else if (cells.length === 6) {
                console.log(`  - Cell[4] is "${cells[4]}" (expected "$")`);
                console.log(`  - Cell[5] is "${cells[5]}" (should be a number)`);
              }
            }
          } else {
            console.log(`✗ Row ${i + 1}: Skipped - wrong number of cells (${cells.length})`);
          }
        }
        
        console.log(`\nSummary:`);
        console.log(`Total processed rows: ${processedRows}`);
        console.log(`Total asset sum: ${assetTotal}`);
        console.log(`Expected sum (30 × 12): ${30 * 12}`);
        
        // Log all processed values in order
        console.log('\nAll processed values:');
        allValues.sort((a, b) => a.row - b.row).forEach(({row, value}) => {
          console.log(`Row ${row}: ${value}`);
        });
        
        // Calculate average value
        const avgValue = allValues.length > 0 ? assetTotal / allValues.length : 0;
        console.log(`\nAverage value per row: ${avgValue}`);
        console.log(`Expected average value: 12`);

        // Update the total value cell if it exists
        for (let i = 0; i < rows.items.length; i++) {
          const row = rows.items[i];
          const cells = row.cells.items.map(cell => cell.body.text.trim());
          
          // Look for the "Total Value of All Property" row
          if (cells.length === 4 && cells[1] === "Total Value of All Property" && cells[2] === "$") {
            const valueCell = row.cells.items[3].body;
            const placeholderPattern = /^[xX\s,.$0]+$/;
            if (placeholderPattern.test(cells[3])) {
              const formattedSum = assetTotal.toLocaleString("en-US", { style: "currency", currency: "USD" });
              valueCell.clear();
              valueCell.insertText(formattedSum, Word.InsertLocation.start);
              await context.sync();
            }
            break;
          }
        }
      }
    });
  } catch (error) {
    console.error("Calculation failed:", error);
    await Word.run(async (context) => {
      context.document.body.insertParagraph(
        "Error: " + error.message,
        Word.InsertLocation.end
      );
      await context.sync();
    });
  } finally {
    event.completed();
  }
}

Office.actions.associate("action", action);
